.faq {
	display: flex;
	justify-content: center;
}
    
.faq__wrapper {
	max-width: 1366px;
	padding: 0 198px;
	width: 100%;
}

.faq__list {
	border-collapse: collapse;
	width: 100%;

	caption {
		text-align: center;
		padding-bottom: 49px;
		font-family: "Cormorant Garamond", "Times New Roman", serif;
		font-size: 34px;
		font-weight: 700;
		line-height: 44.2px;
		letter-spacing: 0.03em;
		margin: 0;
	}

	td {
		border: 1px solid $brown;
		padding: 43px 100px 42px 46px;
		position: relative;
	}

	h3 {
		font-size: 18px;
		line-height: 23.4px;
		font-weight: 500;
		margin: 0;
	}

	p {
		font-weight: 400;
		padding-top: 9px;
		padding-left: 5px;
		margin: 0;
		padding-right: 300px;
		line-height: 20.8px;
	}

	button {
		display: block;
    position: absolute;
    content: "";
    right: 0;
    top: 0;
    width: 100%;
    height: 100%;
    cursor: pointer;
    border: 0;
		background-color: transparent;

		&:hover {
			background-color: $light-brown;
			z-index: -1;
		}

		&:before {
			position: absolute;
			content: "";
			width: 17px;
			height: 10px;
			right: 47px;
			top: 49px;
			background-image: url(../img/icon-arrow-up.svg);
		}
	}
}

.faq__list-item--hide button {
	&:before {
		display: none;
	}

	&:after {
		position: absolute;
		content: "";
		width: 17px;
		height: 10px;
		right: 47px;
		top: 53px;
		background-image: url(../img/icon-arrow-down.svg);
	}
}

.faq__list-item--hide p{
	display: none;
}

