// 	display: none;
// }

.faq--nojs {
	.faq__list p{
		display: block;
	}
	button {
		&:before {
			display: none;
		}

		&:after {
			display: block;
		}
	}
}

@media (max-width: $tablet-width) {
	.faq__wrapper {
		padding: 0 45px;
	}

	.faq__list {
		p {
      padding-right: 0;
      padding-top: 26px;
      padding-left: 2px;
    }
    
    h3 {
      padding-left: 2px;
      letter-spacing: 0.003em;
    }

    td {
      padding-bottom: 44px;
    }

    button {
      &:before {
        width: 26px;
        height: 16px;
        right: 49px;
        top: 47px;
        background-size: cover;
      }

      &:after {
        width: 26px;
        height: 16px;
        right: 49px;
        top: 47px;
        background-size: cover;
      }
    }

  }
}

@media (max-width: $mobile-width) {
	.faq__wrapper {
		padding: 0 15px;
	}

	.faq__list {	
		caption {
			padding-bottom: 27px;
		}

		td {
      padding: 19px 58px 18px 24px;
		}

		h3 {
			font-size: 16px;
			line-height: 22.4px;
      padding-right: 0;
      padding-left: 0px;

			&:last-child {
				padding-right: 0;
			}
		}
	
		p {
			font-size: 14px;
			line-height: 20.6px;
			padding-top: 13px;
			padding-left: 0;
			padding-right: 0;
		}

		button {
			&:before {
        width: 23px;
        right: 23px;
        top: 23px;
        height: 13px;
      }
      
      &:after {
        width: 23px;
        height: 13px;
        right: 23px;
        top: 23px;
      }
		}
  }
  
  // .faq__list-item__countries,
  // .faq__list-item__payment,
  // .faq__list-item__return  {
  //   padding-top: 14px;
  //   padding-top: 22px;
  // }

	.item__return {
		h3 {
			padding-right: 0;
		}
	}
}
